	module.exports = {"data":{"markdownRemark":{"html":"<p>写真の中央にある PC です。 外見は変化がないものの、マザーボードと CPU、メモリをフルに差し替えて 1.7 倍程度の性能へと強化を施したバージョン。現在は一部パーツを<a href=\"/Homebuilt-3\">弐號機</a>に吸収され、足りない分を<a href=\"/Homebuilt-1\">Ver.1</a>より吸収し弟に寄贈。  5年後、ひょんなことで帰ってきたが、Windows 7 が “とりあえず動く” 程度のスペックだったので、Linux サーバ実験機、兼 NAS として運用していた。最終的に VPS へとに移行することになり、この PC はオークションサイトで処分。Pentium4 (Northwood) で NX ビットに対応しておらず、Windows 8 は動作できなかったが、思いのほか良い値段で処分出来た記憶がある。</p>\n<h2>当時はまったPCゲーム</h2>\n<ul>\n<li>\n<p>bemaniaDX</p>\n</li>\n<li>\n<p>Cresteaju</p>\n</li>\n<li>\n<p>DOOMシリーズ</p>\n</li>\n<li>\n<p>RagnarokOnline</p>\n<h2>製作物・他やってたこと</h2>\n</li>\n<li>\n<p>(CGI掲示板)BoardXP</p>\n</li>\n<li>\n<p>(CGI掲示板)Hi-macho</p>\n</li>\n<li>\n<p>(CGIゲーム)EVIL KINGDOM</p>\n</li>\n<li>\n<p>(オリジナルSS)CATS STORY(結局ゲームは頓挫し長編ストーリー小説として完成した)</p>\n</li>\n<li>\n<p>(オリジナルゲーム)<a href=\"/revenger\">REVENGER</a> 製作開始(後の紅魔郷Phや幻寇戦乱録にネタ吸収されお蔵入り)</p>\n<h2>性能</h2>\n</li>\n<li>\n<p>CPU:intel Celeron(Coppermine-128K/600MHz→675MHzオーバークロック)</p>\n</li>\n<li>\n<p>RAM:416MB</p>\n</li>\n<li>\n<p>VGA:nVidia GeForce2MX400(VRAM32MB)</p>\n</li>\n<li>\n<p>HDD:20GB+20GB(JBOD)+4GB</p>\n</li>\n<li>\n<p>CDD:不明(ジャンク品)</p>\n</li>\n<li>\n<p>FDD:3.5inch(3mode)×1</p>\n</li>\n<li>\n<p>NET:10Base-T</p>\n</li>\n<li>\n<p>SOUND:YAMAHA YMF724</p>\n</li>\n<li>\n<p>OS:WindowsXP Professional→Windows7 Ultimate→Fedora</p>\n</li>\n</ul>","frontmatter":{"cover":null,"date":"2001-12-01T00:00:00+09:00","strDate":"2001/11/30","title":"(2002-2003) 世界最速1号Ver.2"}}},"pathContext":{"path":"/ja/blog/2001/12/01-homebuilt-2/","langKey":"ja"}}

